.App {
  padding: 20px;
  width: 960px;
  margin: 0 auto;
}
.container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.container > .left,
.container > .right {
  padding: 12px;
}
.container > .left {
  flex-grow: 0;
  max-width: 33.333333%;
  flex-basis: 33.333333%;
}
.container > .right {
  flex-grow: 0;
  max-width: 66.666667%;
  flex-basis: 66.666667%;
}
.right {
  background-color: #f8f8f8;
}

.fileinput {
  padding: 15px;
  border: 1px solid #202125;
}
.fileinput__error {
  color: red;
  font-size: 0.8em;
  margin-top: 10px;
}

.heading1 {
  font-family: "CircularStd-Bold", sans-serif;
  font-size: 24px;
  line-height: 30px;
  color: #202125;
}
.heading3 {
  font-family: "CircularStd-Medium", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #202125;
}
.heading4 {
  font-family: "CircularStd-Bold", sans-serif;
  font-size: 12px;
  line-height: 22px;
  color: #57cc14;
}
.paragraph {
  font-family: "CircularStd-Book", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #202125;
}

.hourwidget {
  width: 440px;
  padding: 36px 43px 40px;
  background-color: #ffffff;
  border: 1px solid rgba(32, 33, 37, 0.08);
  border-radius: 15px;
  margin: 50px auto;
}
.hourwidget__header {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  padding-bottom: 16px;
  border-bottom: 1.5px solid #202125;
}
.icon__wrapper {
  width: 16px;
  height: 16px;
  margin-right: 16px;
}
.content__row {
  display: flex;
  justify-content: space-between;
  padding: 13px 0;
  border-bottom: 1px solid rgba(32, 33, 37, 0.08);
}
.day__wrapper {
  display: flex;
  align-items: baseline;
}
.day__dayoftheweek {
  text-transform: capitalize;
}
.day__today {
  color: rgb(87, 204, 20);
  margin-left: 7px;
  text-transform: capitalize;
}
.hour.closed {
  color: rgba(32, 33, 37, 0.42);
}

